import React, { useCallback, useState, useEffect } from "react";
import { Container, makeStyles } from "@material-ui/core";
import Page from "src/components/Page";
import InfomationEditForm from "./InfomationEditForm";
import { useHistory } from "react-router";
import { useSnackbar } from "notistack";
import { getInfomationRequest } from "src/actions/infomationActions";
import { useDispatch } from "react-redux";

const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: theme.palette.background.dark,
    minHeight: "100%",
    paddingTop: theme.spacing(3),
    paddingBottom: 100,
  },
}));

function InfomationView(props) {
  const classes = useStyles();
  const dispatch = useDispatch();
  const history = useHistory();
  const [projectDetail, setProjectDetail] = useState([]);
  const { enqueueSnackbar } = useSnackbar();

  const getProjects = useCallback(() => {
    dispatch(
      getInfomationRequest(
        (res) => {
          console.log("getInfomationRequest", res);
          setProjectDetail(res);
        },
        (error) => {
          console.log("error", error);
        }
      )
    );
  }, []);

  useEffect(() => {
    getProjects();
  }, []);
  const handleSubmitSuccess = () => {
    enqueueSnackbar("Cập nhật thành công", {
      variant: "success",
    });
    history.push("/app/management/projects");
  };
  const handleSubmitFailed = () => {
    enqueueSnackbar("Cập nhật thất bại", {
      variant: "error",
    });
  };

  return (
    <Page className={classes.root} title="User Create">
      <Container maxWidth="lg">
        {/* <Header /> */}
        <InfomationEditForm
          projectDetail={projectDetail}
          onSubmitSuccess={handleSubmitSuccess}
          onSubmitFailed={handleSubmitFailed}
        />
      </Container>
    </Page>
  );
}

export default InfomationView;
